import uniStatusBar from '@/components/uni-ui/uni-status-bar/uni-status-bar.vue';
export default {
	name: 'login',
	components: {
		uniStatusBar
	},
	data() {
		return {
			username: '',
			password: '',
			checked:true,
			focusClass: {
				username: false,
				password: false
			},
			//验证规则
			rules: {
				username: [
					{
						rule: /^[a-zA-Z]\w{5,17}$/,
						msg: '以字母开头，长度在6~18之间，只能包含字母、数字和下划线'
					}
				],
				password: [
					{
						rule: /^.{5,20}$/,
						msg: '长度为3-20的所有字符'
					}
				]
			}
		};
	},
	created() {

		uni.hideHomeButton();

	},
	methods: {
		submit() {
			// this.rules['username'].forEach((v)=>{
			// 	if(!v.rule.test(this.username)){
			// 		console.log(v.msg)
			// 	}
			// })
			console.log(this.checked)
			if(!this.validate('username')) return;
			if(!this.validate('password')) return;
			console.log('提交成功')
		},
		goBack(){
			uni.navigateBack()
		},
		//表单验证
		validate(key) {
			let check = true;
			this.rules[key].forEach(v => {
				//验证失败
				if (!v.rule.test(this[key])) {
					// console.log(v.msg)
					uni.showToast({
						title: v.msg,
						icon: 'none'
					});
					check = false;

					return false;
				}
			});
			return check;
		},
		focus(key) {
			console.log(this.focusClass[key]);

			this.focusClass[key] = true;
		},
		blur(key) {
			console.log('失去焦点', this.focusClass[key]);
			this.focusClass[key] = false;
		}
	}
};
